/* How it work page */

.memberContainer {
  display: grid;
  font-family: Roboto;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 350px 350px 350px 400px;
  /* border: 2px solid red; */
}

.item20 {
  grid-area: how-it-works;
  grid-column: 2 / 4;
  grid-row: 1 / 2;
  /* background-color: rgb(245, 152, 71); */
}

    .topPart {
      margin: 60px 0 0 40px;
      /* background-color: yellow; */
    }

    .hello {
      font-family: Roboto Medium;
      font-size: 45px;
      padding-top: 40px;
      margin-bottom: 0;
    }

    .topText {
      font-family: Roboto Light;
      width: 80%;
      font-size: 20px;
    }

    .viewMentor {
      font-size: 25px;
      margin-top: 70px;
    }

.item21 {
  grid-area: jane-profile;
  grid-column: 1 / 2;
  grid-row: 1 / 3;
  /* background-color: rgb(195, 240, 71); */
}

    .janeProfile {
      font-family: PT sans;
      font-size: 18px;
      color: #525252;
      width: 70%;
      border: 1px solid #EEEEEE;
      box-sizing: border-box;
      box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
      border-radius: 10px;
      margin: 40px auto;
    }

    .profileCrop {
      border-radius: 50%;
      overflow: hidden;
      width: 130px;
      height: 130px;
      margin: 20px auto;
      /* border: 1px solid blue; */
    }

    .janePic {
      height: 210px;
      /* border: 1px solid rgb(128, 0, 122); */
    }

    .nameTitle {
      text-align: center;
    }

    .borderBottom {
      border-bottom: 2px solid rgb(218, 216, 216);
      position: relative;
      width: 90%;
      margin: 25px auto;
    }

    .memberUl {
      list-style: none;
      font-family: PT sans;
      font-size: 17px;
      color: grey;
      margin: 40px auto;
      overflow: scroll;
    }

    .memberLi {
      margin-bottom: 20px;
    }

