*/

.marginLeft {
  margin-left: -20px;
}

.redText {
  color: #F55E61;
}

.bubbles {
  position: relative;
  height: 420px;
  margin: -80px 0 0 -60px;
  /* border: 1px solid blue; */
}

.yellowCircleWrap {
  margin: 0 0 0 -60px;
  position: absolute;
  /* To push circle to background */
  z-index: -1;
}

.yellowCircle {
  height: 200px;
}

.ad {
  width: 70%;
  margin: auto;
  text-align: left;
}

.sponserTitle {
  font-size: 24px;
  color: #525252;
}

.eventPic {
  overflow: hidden;
  border-radius: 10px;
  width: 99%;
  height: 140px;
  margin-top: 20px;
}

.boxRed {
  width: 80%;
  height: 90%;
  border-radius: 5%;
  margin: 15px auto;
  border: 2px solid #F55E61;
  font-family: PT sans;
}

.boxPink {
  width: 80%;
  height: 90%;
  margin: 15px auto;
  border: 3px solid #F5EDED;
  box-sizing: border-box;
  box-shadow: 0px 2px 0px #F5EDED;
  border-radius: 10px;
  font-family: PT sans;
}

.request {
  /* position: absolute; */
  width: 145px;
  height: 25px;
  margin: auto;
  text-align: center;
  background-color: #F55E61;
  color: white;
  border-radius: 5px;
  padding-top: 5px;
}